<template>
  <v-container class="container" fluid>
    <Header />
    <Toolbar />
    <div class="cartDiv">
      <div class="titleCardDiv">
        <h1 class="cartTitle">–í–∞—à–∞ –∫–æ—Ä–∑–∏–Ω–∞</h1>
      </div>
      <v-simple-table class="cartTable">
        <template v-slot:default>
          <tbody>
            <tr v-for="item in Cart" :key="item.name">
              <td class="first">
                <img :src="item.image" alt="#" class="itemPic" />
                <h4>{{ item.name }}</h4>
              </td>
              <td class="text-xs-center">
                <v-btn class="minus" @click="decrement(item)">-</v-btn>
                {{ item.amount }}
                <v-btn class="plus" @click="increment(item)">+</v-btn>
              </td>
              <td class="text-xs-center">{{ item.price * item.amount }} —Ä—É–±.</td>
              <td class="text-xs-right">
                <v-btn @click="removeProduct(item)" class="removeBtn">
                  <i class="material-icons">delete_forever</i> –£–±—Ä–∞—Ç—å –∏–∑ –∫–æ—Ä–∑–∏–Ω—ã
                </v-btn>
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <div class="summingDiv" v-if="Cart.length">
        <h1 class="summing">
          –ò—Ç–æ–≥–æ:
          <span>{{ sumUp() }}</span> —Ä—É–±.
        </h1>
        <v-btn class="makeOder" @click="inputData()">–û—Ñ–æ—Ä–º–∏—Ç—å –∑–∞–∫–∞–∑</v-btn>
      </div>
      <div class="orderDiv" v-if="show && Cart.length">
        <v-form ref="form" v-model="valid" class="orderForm" lazy-validation>
          <div class="inputFields">
            <div class="inputDiv">
              <div class="firstGroup">
                <v-text-field
                  :single-line="boulen"
                  class="inputField"
                  v-model="name"
                  :rules="nameRules"
                  label="Name"
                  required
                ></v-text-field>
                <v-text-field
                  :single-line="boulen"
                  class="inputField"
                  v-model="sername"
                  :rules="nameRules"
                  label="Sername"
                  required
                ></v-text-field>
              </div>
              <div class="secondGroup">
                <v-text-field
                  :single-line="boulen"
                  class="inputField"
                  v-model="phone"
                  label="Phone"
                  required
                ></v-text-field>
                <v-text-field
                  :single-line="boulen"
                  class="inputField"
                  v-model="email"
                  :rules="emailRules"
                  label="E-mail"
                  required
                ></v-text-field>
              </div>
            </div>
            <v-select
              :items="this.items"
              :single-line="boulen"
              label="–°–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã"
              class="selection"
            ></v-select>
          </div>
        </v-form>
      </div>
    </div>
    <div class="div">
      <v-btn
        :disabled="!valid"
        color="success"
        class="submitBtn"
        @click="pay()"
        v-if="show && Cart.length"
      >–ü–µ—Ä–µ–π—Ç–∏ –∫ –æ–ø–ª–∞—Ç–µ</v-btn>
    </div>

    <Footer />
  </v-container>
</template>

<script>
import Header from "../components/Header";
import Toolbar from "../components/Toolbar";
import Footer from "../components/Footer";

import axios from "axios";
export default {
  name: "CartView",
  components: {
    Header,
    Toolbar,
    Footer
  },
  data: () => ({
    show: false,
    valid: true,
    name: "",
    sername: "",
    phone: "",
    boulen: true,
    nameRules: [
      v => !!v || "Name is required",
      v => (v && v.length <= 10) || "Name must be less than 10 characters"
    ],
    email: "",
    emailRules: [
      v => !!v || "E-mail is required",
      v => /.+@.+/.test(v) || "E-mail must be valid"
    ],
    select: "–°–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã",
    items: ["–°–ø–æ—Å–æ–± –æ–ø–ª–∞—Ç—ã", "–ù–∞–ª–∏—á–Ω—ã–µ", "–ë–∞–Ω–∫–æ–≤—Å–∫–æ–π –∫–∞—Ä—Ç–æ–π"]
  }),
  computed: {
    Cart() {
      return this.$store.getters.cart;
    }
  },

  created() {
      this.$store.dispatch("loadCart");
  },

  methods: {
    validate() {
      if (this.$refs.form.validate()) {
        this.snackbar = true;
      }
    },

    reset() {
      this.$refs.form.reset();
    },

    resetValidation() {
      this.$refs.form.resetValidation();
    },

    removeProduct(product) {
      this.$store.commit("removeProduct", product);
    },

    decrement(item) {
      this.$store.dispatch("decrementCart", item);
    },

    increment(item) {
      this.$store.dispatch("incrementCart", item);
    },

    sumUp() {
      let result = 0;
      this.Cart.forEach(elem => {
        let final = elem.price * elem.amount;
        result += final;
      });
      return result;
    },

    inputData() {
      this.show = true;
    },

    pay() {
      fetch("https://area51dev.studiomyka.ru/orders/buy/", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ PurchaseAmt: this.sumUp() })
      }).then(response => {
        console.log(response);
      });
    }
  }
};
</script>

