<script>
import moment from "moment";

export default {
  name: "MasterClasses",
  props: ["masterClasses"],
  data: () => ({
    modal: false,
    re: /\D\d\d/
  }),
  methods: {
    showModal(item) {
      this.$store.dispatch("showDetails", item);
    },

    momentDate(item) {
      return new Date(item).toLocaleString("ru", {
        month: "long",
        day: "numeric"
      });
    },

    momentTime(item) {
      return new Date(item).toLocaleString("ru", {
        hour: "numeric",
