import axios from "axios";

import swal from 'sweetalert2' //–ø–ª–∞–≥–∏–Ω –¥–ª—è –≤—ã–∑—ã–≤–∞ –ø–æ–ø-–æ–∫–Ω–∞ –ø–æ—Å–ª–µ –æ—Ç–ø—Ä–∞–≤–∫–∏ –∑–∞—è–≤–æ–∫ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
const now = new Date();

export default {
    state: {
        master–°lasses: [],
        cartItems: [],
        masterDetails: [],
        masterclassesHomePage: [],
        kitchen: []
    },
    mutations: {
        updateMasterClasses(state, items) {
            state.master–°lasses = items;
        },

        updateMasterClassesHomePage(state, items) {
            state.masterclassesHomePage = items;
        },

        addToMasterDetails(state, item) {
            if (state.masterDetails.length > 0) {
                state.masterDetails.splice(0);
                state.masterDetails.push(item);
            } else {
                state.masterDetails.push(item);
            }
            const parsed = JSON.stringify(state.masterDetails);
            localStorage.setItem("masterClassDetails", parsed);
        },

        updateKitchen(state, item) {
            let type = '–í—ã–±—Ä–∞—Ç—å –∫—É—Ö–Ω—é';
            state.kitchen.unshift(type);
            state.kitchen.push(item);
        },

        loadCartState(state, items) {
            state.cartItems = items;
        },
    },


    actions: {
        getKitchenTypes(context, payload) {
            axios.get('https://area51dev.studiomyka.ru/product/kitchen/').then((response) => {
                response.data.forEach(element => {
                    context.commit('updateKitchen', element.name);
                });
            })
        },

        saveRequest(context, payload) {
            fetch('https://area51dev.studiomyka.ru/form/petition/', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(payload) })
                .then(response => {
                    if (response.status >= 200 && response.status <= 202) {
                        swal.fire({
                            type: 'success',
                            title: '–í–∞—à–∞ –∑–∞—è–≤–∫–∞ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞!',
                            text: '–ú–µ–Ω–µ–¥–∂–µ—Ä —Å–∫–æ—Ä–æ —Å –í–∞–º–∏ —Å–≤—è–∂–µ—Ç—Å—è.',
                            confirmButtonColor: '#895aac',
                        })
                    } else {
                        swal.fire({
                            image: 'image-class',
                            title: 'Oops...',
                            text: '–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫!',
                            confirmButtonColor: '#895aac',
                        })
                    }
                })
        },

        saveMCRequest(context, payload) {
            const prod = JSON.parse(localStorage.getItem("masterClassDetails"))
            fetch('https://area51dev.studiomyka.ru/form/formact/', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(payload) })
                .then(response => {
                    if (response.status >= 200 && response.status <= 202) {
                        swal.fire({
                            type: 'success',
                            html: `<p>–í—ã –∑–∞–ø–∏—Å–∞–Ω—ã –Ω–∞ –º–∞—Å—Ç–µ—Ä-–∫–ª–∞—Å—Å: </p> <h5>"${prod[0].name}"</h5><p> –î–∞—Ç–∞ –ø—Ä–æ–≤–µ–¥–µ–Ω–∏—è: </p><h5>${prod[0].dateofevent}.</h5>`,
                            confirmButtonColor: '#895aac'
                        })
                    } else {
                        swal.fire({
                            type: 'error',
                            title: 'Oops...',
                            text: '–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫!',
                            confirmButtonColor: '#895aac'
                        })
                    }
                })
        },

        masterClassesList(context, items) {
            axios.get(`https://area51dev.studiomyka.ru/product/filter/?available=true`).then((response) => {
                context.commit('updateMasterClasses', response.data);
            })
        },

        masterClassesListHomePage(context, items) {
            axios.get(`https://area51dev.studiomyka.ru/product/filter/?available=true`).then((response) => {
                context.commit('updateMasterClassesHomePage', response.data);
            })
        },

        loadCart(context) {
            axios.get('https://area51dev.studiomyka.ru/orders/cart/')
                .then((response) => {
                    context.commit('loadCartState', response.data);
                })
        },

        showDetails(context, item) {
            context.commit('addToMasterDetails', item);
        },
    },
    getters: {
        masterClasses(state) {
            return state.master–°lasses;
        },

        masterClassesHomePage(state) {
            return state.masterclassesHomePage;
        },

        cart(state) {
            return state.cartItems;
        },

        masterClassDetails(state) {
            return state.masterDetails;
        },

        kitchen(state) {
            return state.kitchen;
        }
    }
}