<script>
import li1 from "../img/li.png";
import bonus from "../img/certificate.svg";
import gift from "../img/gift.png";
import cake from "../img/cake.png";
import wallet from "../img/wallet.png";
import backBtn from "../components/backBtn";
import calendar from "../img/calendar.png";
import MCRequest from "../components/MCmodalRequest";
export default {
  name: "MasterClassModal",
  components: {
    backBtn,
    MCRequest
  },
  data: () => ({
    bonus: bonus,
    details: [],
    li1: li1,
    wallet: wallet,
    calendar: calendar,
    cake: cake,
    gift: gift,
    re: /\D\d\d/
  }),
  methods: {
    addMasterToCart(item) {
      this.$store.dispatch("addMasterToCart", item);
    },

    momentDate(item) {
      return new Date(item).toLocaleString("ru", {
        month: "long",
        day: "numeric"
      });
    },

    momentTime(item) {
      return new Date(item).toLocaleString("ru", {
        hour: "numeric",
        minute: "numeric"
      });
    }
  },

  mounted() {
    if (localStorage.getItem("masterClassDetails")) {
      try {
        this.details = JSON.parse(localStorage.getItem("masterClassDetails"));
      } catch (e) {
        localStorage.removeItem("masterClassDetails");
      }
    }
  }
};
