        return pattern.test(value) || "–ù–µ–≤–µ—Ä–Ω—ã–π E-mail";
      },
      phone: value => {
        const pattern = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/;
        return pattern.test(value) || "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –Ω–æ–º–µ—Ä–∞";
      },
