        placeholder="–§–∞–º–∏–ª–∏—è"
        class="name"
        required
      ></v-text-field>
      <v-text-field class="phone" placeholder="–¢–µ–ª–µ—Ñ–æ–Ω*" v-model="phone" :rules="[rules.phone]"></v-text-field>
      <v-text-field
        class="email"
        placeholder="Email*"
        v-model="email"
        :rules="[rules.required, rules.email]"
      ></v-text-field>
      <v-card-actions>
        <v-btn text @click="$bvModal.hide('MCRequest'), submitMC(), reset()" class="logInMdBtn">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</v-btn>
      </v-card-actions>
