<script>
import logo from "../img/logoFake1.png";

import phoneIcon from "../img/iconsPhoneTube.png";
import mailIcon from "../img/icMail.png";

import iconFace from "../img/faceIc.png";
import groupInst from "../img/groupInst.png";
export default {
  data: () => ({
    logo: logo,
    iconFace: iconFace,
    groupInst: groupInst,
    phoneIcon: phoneIcon,
    mailIcon: mailIcon,
    serName: "",
    rules: {
      required: value => !!value || "E-mail –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω",
      email: value => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return pattern.test(value) || "–ù–µ–≤–µ—Ä–Ω—ã–π E-mail";
      },
      phone: value => {
        const pattern = /^((8|\+7)[\- ]?)?(\(?\d{3}\)?[\- ]?)?[\d\- ]{7,10}$/;
        return pattern.test(value) || "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –Ω–æ–º–µ—Ä–∞";
      },
      name: value => {
        const pattern = /^[a-zA-Z–∞-—è–ê-–Ø]+$/ui;
        return pattern.test(value) || "–ù–µ–≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç";
      },
      nameRules: v => !!v || "–í–≤–µ–¥–∏—Ç–µ –∏–º—è"
    },
    name: "",
    email: "",
    phone: "",
    nameRules: [
      v => !!v || "–í–≤–µ–¥–∏—Ç–µ –∏–º—è",
      v => (v && v.length <= 10) || "–ò–º—è –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –º–µ–Ω—å—à–µ 10 —Å–∏–º–≤–æ–ª–æ–≤"
    ],
    lastnameRules: [v => !!v || "–í–≤–µ–¥–∏—Ç–µ —Ñ–∞–º–∏–ª–∏—é"]
  }),

  methods: {
    submitMC() {
      let id = JSON.parse(localStorage.getItem("masterClassDetails"));
      this.$store.dispatch("saveMCRequest", {
        first_name: this.name,
        last_name: this.serName,
        phone_number1: this.phone,
        email: this.email,
        product_id: id[0].id // TODO: update me
      });
    },

     reset () {
        this.$refs.form.reset()
      },
  }
};
</script>

