    <v-layout class="mastClassText">
      <p>
        –ù–∞ –º–∞—Å—Ç–µ—Ä-–∫–ª–∞—Å—Å–∞—Ö –≤—ã –±—É–¥–µ—Ç–µ –Ω–∞—Ö–æ–¥–∏—Ç—å—Å—è –Ω–∞ —Å–≤–æ–µ–π ¬´–ª–∏—á–Ω–æ–π —Ç–µ—Ä—Ä–∏—Ç–æ—Ä–∏–∏¬ª, —Å –∏–Ω–¥–∏–≤–∏–¥—É–∞–ª—å–Ω—ã–º –æ–±–æ—Ä—É–¥–æ–≤–∞–Ω–∏–µ–º, –∞–∫—Å–µ—Å—Å—É–∞—Ä–∞–º–∏ –∏ –ø–æ–¥–≥–æ—Ç–æ–≤–ª–µ–Ω–Ω—ã–º–∏ —Å–ø–µ—Ü–∏–∞–ª—å–Ω–æ –¥–ª—è –í–∞—Å –ø—Ä–æ–¥—É–∫—Ç–∞–º–∏.
        <span
          v-if="showText"
        >
          –ù–∏–∫–∞–∫–∏—Ö ¬´–æ–±—â–∏—Ö –∫–æ—Ç–ª–æ–≤¬ª! –®–µ—Ñ-–ø–æ–≤–∞—Ä —É–¥–µ–ª–∏—Ç –≤–Ω–∏–º–∞–Ω–∏–µ –∞–±—Å–æ–ª—é—Ç–Ω–æ –∫–∞–∂–¥–æ–º—É —É—á–∞—Å—Ç–Ω–∏–∫—É –∏ –ø–æ–Ω—è—Ç–Ω–æ —Ä–∞—Å—Å–∫–∞–∂–µ—Ç, –∏ –ø–æ–∫–∞–∂–µ—Ç –≤—Å–µ, —á—Ç–æ ¬´–Ω–µ –≤–∏–¥–Ω–æ –º–µ–∂–¥—É —Å—Ç—Ä–æ–∫¬ª –≤ –∫—É–ª–∏–Ω–∞—Ä–Ω—ã—Ö –∫–Ω–∏–≥–∞—Ö –∏ –ø—Ä–æ–≥—Ä–∞–º–º–∞—Ö, –æ—Ç–≤–µ—Ç–∏—Ç –Ω–∞ –≤—Å–µ –≤–æ–∑–Ω–∏–∫—à–∏–µ –≤–æ–ø—Ä–æ—Å—ã –∏ –≤—Ä—É—á–∏—Ç –∏–º–µ–Ω–Ω–æ–π –¥–∏–ø–ª–æ–º –æ –ø—Ä–æ—Ö–æ–∂–¥–µ–Ω–∏–∏ –º–∞—Å—Ç–µ—Ä-–∫–ª–∞—Å—Å–∞.
          –ü–æ –æ–∫–æ–Ω—á–∞–Ω–∏–∏ –º–∞—Å—Ç–µ—Ä-–∫–ª–∞—Å—Å–∞ —É—á–∞—Å—Ç–Ω–∏–∫–∏ —Å–æ–±–∏—Ä–∞—é—Ç—Å—è –∑–∞ –±–æ–ª—å—à–∏–º —Å—Ç–æ–ª–æ–º, —á—Ç–æ–±—ã –Ω–∞—Å–ª–∞–¥–∏—Ç—å—Å—è –ø—Ä–∏–≥–æ—Ç–æ–≤–ª–µ–Ω–Ω–æ–π –µ–¥–æ–π. –ö–∞–∂–¥—ã–π –º–∞—Å—Ç–µ—Ä-–∫–ª–∞—Å—Å ‚Äì —ç—Ç–æ –ø–æ–ª–Ω–æ–µ –ø–æ–≥—Ä—É–∂–µ–Ω–∏–µ –≤ –ø—Ä–æ—Ü–µ—Å—Å! –í—ã —É–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ –ø—Ä–∏–≥–æ—Ç–æ–≤–ª–µ–Ω–∏–µ –≤–∫—É—Å–Ω–æ–π, —è—Ä–∫–æ–π –∏ –ø–æ–ª–µ–∑–Ω–æ–π –µ–¥—ã ‚Äì —ç—Ç–æ –Ω–µ –ú–£ÃÅ–ö–ê, –∞ –∏–Ω—Ç–µ—Ä–µ—Å–Ω—ã–π –ø—Ä–æ—Ü–µ—Å—Å, –∫–æ—Ç–æ—Ä—ã–π –º–æ–∂–µ—Ç –¥–æ—Å—Ç–∞–≤–ª—è—Ç—å —É–¥–æ–≤–æ–ª—å—Å—Ç–≤–∏–µ.
        </span>
        <button @click="showTextContent()" class="arrowRight">
          <v-icon dark v-if="!showText">reply_all</v-icon>
        </button>
        <button @click="hideTextContent()" class="arrowLeft">
          <v-icon dark v-if="showText">reply_all</v-icon>
        </button>
      </p>
    </v-layout>
    <v-layout class="products">
      <v-layout wrap align-center class="filterWrapper">
        <v-flex xs12 sm6 md3 class="filterLine">
          <v-select
            class="filter"
            :items="['–í—ã–±—Ä–∞—Ç—å —Å—Ç–æ–∏–º–æ—Å—Ç—å', '–û—Ç 1000 –¥–æ 3000', '–û—Ç 3000 –¥–æ 5000', '–û—Ç 5000 –¥–æ 6000']"
            v-model="type"
            @change="filterByPrice"
            suffix
          ></v-select>
        </v-flex>
        <v-flex xs12 sm6 md3 class="filterLine">
          <v-select
            class="filter"
            :items="kitchen"
            v-model="type1"
            @change="filterByKitchen"
            suffix
          ></v-select>
        </v-flex>
        <v-flex xs12 sm6 md3 class="pickerLine">
          <v-menu
            v-model="menu"
            :close-on-content-click="false"
            :nudge-right="40"
            transition="scale-transition"
            offset-y
            full-width
            min-width="290px"
            class="menu"
          >
            <template v-slot:activator="{ on }">
              <v-text-field :value="transformDate" prepend-icon="event" readonly v-on="on"></v-text-field>
            </template>
            <!-- <v-btn class="refresh" @click="refresh()">–°–±—Ä–æ—Å–∏—Ç—å –¥–∞—Ç—É</v-btn> -->
            <v-date-picker
              v-model="date"
              @input="menu = false"
              locale="ru"
              @change="showMasterClass"
            ></v-date-picker>
          </v-menu>
        </v-flex>
      </v-layout>
      <MasterClasses
        :masterClasses="MasterClasses"
        v-if="show"
        :search="this.search"
        :showModal="showModal"
      />
      <MasterClasses :masterClasses="filtred" v-if="!show" :search="this.search" />
      <MasterClasses :masterClasses="filteredItems" v-if="filter" :search="this.search" />
    </v-layout>
    <Partners />
    <MobContacts />
    <Footer />
  </v-container>
</template>

<script>
import Header from "../components/Header";
import Toolbar from "../components/Toolbar";
import Partners from "../components/Partners";
import Footer from "../components/Footer";
import MasterClasses from "../components/MasterClasses";
import MobContacts from "../components/MobContacts";

import moment from "moment";

export default {
  name: "MasterClassesPage",
  components: {
    Header,
    Toolbar,
    Partners,
    MasterClasses,
    MobContacts,
    Footer
  },
  data: () => ({
    type: "–í—ã–±—Ä–∞—Ç—å —Å—Ç–æ–∏–º–æ—Å—Ç—å",
    type1: "–í—ã–±—Ä–∞—Ç—å –∫—É—Ö–Ω—é",
    search: "",
    date: new Date().toISOString().substr(0, 10),
    menu: false,
    modal: false,
    filtred: [],
    show: true,
    filter: false,
    cartItems: [],
    a: "",
    showText: false
  }),
  computed: {
    MasterClasses() {
      return this.$store.getters.masterClasses;
    },

    Cart() {
      return this.$store.getters.cart;
    },

    transformDate: function() {
      return this.date
        ? moment(this.date)
            .locale("ru")
            .format("LL")
        : "";
    },

    kitchen() {
      return this.$store.getters.kitchen;
    }
  },

  created() {
    //  this.$store.dispatch("loadCart");
    this.$store.dispatch("masterClassesList");
    this.$store.dispatch("getKitchenTypes");
  },

  methods: {
    showTextContent() {
      this.showText = true;
    },

    hideTextContent() {
      this.showText = false;
    },

    showModal(item) {
      this.$store.dispatch("showDetails", item);
      const parsed = JSON.stringify(this.MasterClasses);
      localStorage.setItem("MasterClasses", parsed);
    },

    showMasterClass() {
      let newDate = this.date;

      function containsElem(element) {
        return element.dateofevent.substr(0, 10) !== newDate;
      }

      if (this.MasterClasses.every(containsElem)) {
        this.show = false;
        this.filtred.splice(0);
      }

      this.MasterClasses.forEach(e => {
        if (e.dateofevent.substr(0, 10) === newDate) {
          this.show = false;
          if (!this.filtred.includes(e)) {
            this.filtred.push(e);
          }
          this.filtred.forEach(i => {
            if (!i.dateofevent.substr(0, 10) === newDate) {
              this.filtred.splice(0);
              this.filtred.push(e);
            }
          });
        }
      });
    },

    filterByPrice() {
      let typeS = this.type;
      let arr;
      let arr2 = [];
      arr = this.MasterClasses;
      switch (typeS) {
        case "–û—Ç 1000 –¥–æ 3000":
          this.show = false;
          arr.forEach(c => {
            if (c.price <= 3000 && c.price >= 1000) {
              arr2.push(c);
            }
          });
          arr2.sort(function(a, b) {
            if (a.price > b.price) {
              return 1;
            }
            if (a.price < b.price) {
              return -1;
            }
            return 0;
          });
          return (this.filtred = arr2);
          break;
        case "–û—Ç 3000 –¥–æ 5000":
          this.show = false;
          arr.forEach(c => {
            if (c.price <= 5000 && c.price >= 3000) {
              arr2.push(c);
            }
          });
          arr2.sort(function(a, b) {
            if (a.price > b.price) {
              return 1;
            }
            if (a.price < b.price) {
              return -1;
            }
            return 0;
          });
          return (this.filtred = arr2);
          break;
        case "–û—Ç 5000 –¥–æ 6000":
          this.show = false;
          arr.forEach(c => {
            if (c.price <= 6000 && c.price >= 5000) {
              arr2.push(c);
            }
          });
          arr2.sort(function(a, b) {
            if (a.price > b.price) {
              return 1;
            }
            if (a.price < b.price) {
              return -1;
            }
            return 0;
          });
          return (this.filtred = arr2);
          break;
        case "–í—ã–±—Ä–∞—Ç—å —Å—Ç–æ–∏–º–æ—Å—Ç—å":
          this.show = true;
          break;
        default:
          break;
      }
    },

    filterByKitchen() {
      let typeS = this.type1;
      let arr;
      let arr2 = [];
      arr = this.MasterClasses;
      this.show = false;
      arr.forEach(elem => {
        if (elem.kitchen.name === typeS && typeS !== "–í—ã–±—Ä–∞—Ç—å –∫—É—Ö–Ω—é") {
          arr2.push(elem);
        }
        this.filtred = arr2;
      });

      if (typeS === "–í—ã–±—Ä–∞—Ç—å –∫—É—Ö–Ω—é") {
        this.show = true;
      }
    },

    refresh() {
      this.show = true;
      this.menu = false;
    }
  }
};
</script>

